@use '../abstracts' as *;
/*----------------------------------------*/
/*  12. BRAND CSS START
/*----------------------------------------*/

.brand{
  &__slider{
    border-top: 1px solid $border;
  }
  &__item{
    text-align: center;
    padding: 50px 0;
    & img{
      width: inherit !important;
      display: inline-block !important;
      opacity: .2;
    }
    &:hover{
      & img{
        opacity: .8;
      }
    }
  }
}
