@use '../abstracts' as *;
/*----------------------------------------*/
/*  09. SUBSCRIBE CSS START
/*----------------------------------------*/

.subscribe{
    &__area{}
    &__content{
      @media #{$md}{
        margin-bottom: 30px;
      }
      @media #{$sm}{
        margin-bottom: 30px;
      }
      @media #{$xs}{
        margin-bottom: 30px;
      }
        & h4{
            text-transform: capitalize;
            font-size: 24px;
            font-weight: 500;
            margin-bottom: 5px;
        }
        & p{
            margin: 0;
            font-weight: 400;
            color: $black;
            & span{
                font-weight: 500;
            }
        }
    }
    &__form{
        width: 85%;
        position: relative;
        @media #{$md}{
          float: left;
          width: 100%;
        }
        @media #{$sm}{
          float: left;
          width: 100%;
        }
        @media #{$xs}{
          float: left;
          width: 100%;
        }
        & input{
            width: 100%;
            height: 50px;
            line-height: 46px;
            padding-left: 20px;
            padding-right: 120px;
            border: 0;
            color: #979ca5;
            background: 0 0;
            border-radius: 30px;
            border: 2px solid #e4e7f0;
            &::placeholder{
                color: #979ca5;
                font-size: 14px;
            }
        }
        & button{
            position: absolute;
            top: 0;
            right: 0;
        }
    }
    &__icon{
      @media #{$xs}{
				margin-bottom: 15px;
			}
    }
    &__border{
      border-top: 1px solid $border;
    }
}
