@use '../abstracts' as *;

/*----------------------------------------*/


/*  06. SHOP CSS START
/*----------------------------------------*/

.product {
    &__slider {
        & .owl-nav {
            display: flex;
            position: absolute;
            top: -77px;
            right: 0;
            @media #{$xs} {
                display: none;
            }
            & div {
                margin: 0 7px;
                & button {
                    font-size: 24px;
                    color: #333;
                    background: transparent;
                    @include transition(.1s);
                    &:hover {
                        color: $t-yellow;
                        @include transform(scale(1.7));
                    }
                }
            }
        }
    }
    &__item {
        position: relative;
        padding: 15px 10px;
        padding-bottom: 30px;
        @include transition(.3s);
        overflow: hidden;
        &.product__item-2 {
            border: 2px solid $border;
            &:hover {
                border-color: $t-yellow;
                @include box-shadow(none);
            }
        }
        &:hover {
            @include box-shadow(0 0 20px 0 rgba(155, 166, 200, .2));
            & .product__thumb {
                & .second-img {
                    visibility: visible;
                    opacity: 1;
                    @include transform(scale(1));
                }
                & .product__action {
                    visibility: visible;
                    opacity: 1;
                    bottom: 20px;
                }
            }
            & .product__add-btn {
                bottom: 35px;
                visibility: visible;
                opacity: 1;
                &-4 {
                    bottom: 10px;
                }
            }
        }
        &-4 {
            padding-bottom: 0;
            &:hover {
                @include box-shadow(none);
            }
        }
        &-sidebar {
            padding: 15px 0;
            border-bottom: 1px solid $border;
        }
    }
    &__sale {
        padding: 15px 10px;
        padding-bottom: 0;
    }
    &__thumb {
        & .second-img {
            position: absolute;
            top: 0;
            left: 0;
            @include transform(scale(.8));
            opacity: 0;
            visibility: hidden;
        }
        &-sale {
            width: 41.5%;
            @media #{$laptop} {
                width: 120px;
                height: 120px;
            }
            @media #{$lg} {
                width: 120px;
                height: 120px;
            }
            & img {
                width: 125px !important;
            }
        }
        &-sidebar {
            & img {
                width: 100px !important;
                @media #{$laptop, $lg} {
                    width: 70px !important;
                }
            }
        }
    }
    &__action {
        bottom: -40px;
        left: 0;
        right: 0;
        margin: auto;
        text-align: center;
        @include transition(.3s);
        visibility: hidden;
        opacity: 0;
        & ul {
            & li {
                display: inline-block;
                margin: 0 2px;
                & a {
                    display: inline-block;
                    width: 40px;
                    height: 40px;
                    line-height: 40px;
                    border: 1px solid $border-2;
                    background: $grey-3;
                    @include border-radius(50%);
                    text-align: center;
                    font-size: 16px;
                    color: $black;
                    &:hover {
                        background: $t-yellow;
                        color: $white;
                        border-color: $t-yellow;
                    }
                }
            }
        }
        &-4 {
            & ul {
                & li {
                    & a {
                        &:hover {
                            background: $t-red-dark;
                            border-color: $t-red-dark;
                        }
                    }
                }
            }
        }
    }
    &__content {
        & h6 {
            padding-top: 15px;
            font-size: 14px;
            font-weight: 400;
            color: $t-blue;
            line-height: 18px;
            margin-bottom: 2px;
            &:hover {
                & a {
                    text-decoration: underline;
                }
            }
        }
        & span {
            font-size: 16px;
            font-weight: 500;
            color: $black;
            padding: 0 7px;
            display: inline-block;
            &.new {
                color: $t-yellow;
                &-2 {
                    color: $black;
                }
            }
            &.price-old {
                color: $black-soft;
                font-weight: 400;
            }
        }
        & p {
            padding: 0 30px;
        }
        &-2 {
            & h6 {
                font-size: 16px;
                font-weight: 600;
                margin-bottom: 10px;
            }
            & p {
                padding: 0;
            }
        }
        &-4 {
            & h6 {
                color: $black;
            }
            & span.new {
                color: $t-red-dark;
            }
        }
    }
    &__add-btn {
        position: absolute;
        bottom: -40px;
        right: 0;
        left: 0;
        margin: 0;
        text-align: center;
        visibility: hidden;
        opacity: 0;
        @include transition(.2s);
        & button {
            font-size: 12px;
            font-weight: 500;
            background-color: $t-yellow;
            line-height: 45px;
            padding: 0 50px;
            color: $white;
            border-radius: 30px;
            text-transform: initial;
            z-index: 1;
            text-transform: uppercase;
            @media #{$laptop} {
                padding: 0 30px;
            }
            &:hover {
                background: $black;
                color: $white;
            }
        }
        &-4 {
            & button {
                border: 2px solid $t-red-dark;
                background: $t-red-dark;
                color: $white;
                &:hover {
                    background: $white;
                    color: $t-red-dark;
                }
            }
        }
    }
    &__nav-tab {
        @media #{$xs} {
            margin-right: 0;
        }
        & .nav-tabs {
            border: none;
        }
        & .nav-item {
            margin-right: 35px;
            @media #{$sm} {
                margin-right: 30px;
            }
            @media #{$xs} {
                margin-bottom: 5px;
            }
            &:last-child {
                margin-right: 0;
            }
        }
        & .nav-link {
            font-size: 14px;
            color: $black;
            text-transform: capitalize;
            padding: 0;
            background: transparent;
            border: none;
            &.active {
                color: $t-yellow;
                background: transparent;
            }
        }
        &-red {
            & .nav-link {
                &:hover {
                    color: $t-red-dark;
                }
                &.active {
                    color: $t-red-dark;
                }
            }
        }
        &-3 {
            @media #{$md} {
                margin-top: 20px;
            }
            @media #{$sm} {
                margin-top: 20px;
                margin-right: 0;
            }
            @media #{$xs} {
                margin-top: 20px;
                margin-right: 0;
            }
        }
    }
    &__stock {
        & span {
            font-size: 14px;
            color: $t-yellow;
            &:first-child {
                color: $black;
                padding-right: 5px;
            }
        }
    }
    &__add-review {
        & span {
            & a {
                position: relative;
                padding-right: 10px;
                margin-right: 10px;
                &::after {
                    position: absolute;
                    content: '';
                    right: 0;
                    top: 50%;
                    @include transform(translateY(-50%));
                    width: 1px;
                    height: 80%;
                    background: $border;
                }
                &:hover {
                    color: $t-yellow;
                }
            }
            &:last-child {
                & a {
                    &::after {
                        display: none;
                    }
                }
            }
        }
    }
    &__price {
        & span {
            color: $black;
            font-size: 24px;
            font-weight: 500;
            display: inline-block;
            margin-bottom: 22px;
        }
        &-slider {
            & input {
                font-size: 16px;
                font-weight: 500;
                color: $t-yellow;
                border: none;
                outline: none;
                background: none;
                margin-left: 10px;
                width: 100px;
                margin-bottom: 15px;
            }
        }
    }
    &__offer {
        position: absolute;
        top: 0px;
        right: 10px;
        & span {
            display: inline-block;
            text-align: center;
            font-size: 12px;
            padding: 2px 10px;
            border-radius: 2px;
            background-color: $t-red-dark;
            color: $white;
            font-weight: 400;
            &.discount-2 {
                padding: 0px 10px;
            }
        }
    }
    &__countdown {
        padding-bottom: 25px;
        & h4 {
            font-size: 16px;
            margin-bottom: 15px;
        }
        & ul {
            & li {
                display: inline-block;
                width: 60px;
                height: 60px;
                text-align: center;
                background: $grey;
                @include border-radius(50%);
                line-height: 1;
                padding: 13px 0;
                margin-right: 10px;
                & span {
                    font-size: 14px;
                }
                & p {
                    font-size: 12px;
                    margin-bottom: 0;
                    text-transform: capitalize;
                }
            }
        }
    }
    &__deal {
        & .owl-nav {
            display: flex;
            position: absolute;
            top: -77px;
            right: 0;
            & div {
                margin: 0 7px;
                & button {
                    font-size: 24px;
                    color: #333;
                    background: transparent;
                    @include transition(.1s);
                    &:hover {
                        color: $t-yellow;
                        @include transform(scale(1.7));
                    }
                }
            }
        }
    }
    &__electronic {
        & .owl-nav {
            display: flex;
            position: absolute;
            top: -77px;
            right: 0;
            & div {
                margin: 0 7px;
                & button {
                    font-size: 24px;
                    color: #333;
                    background: transparent;
                    @include transition(.1s);
                    &:hover {
                        color: $t-yellow;
                        @include transform(scale(1.7));
                    }
                }
            }
        }
    }
    &__phone {
        &-slider {
            & .owl-nav {
                right: -285px;
                @media #{$laptop} {
                    right: -230px;
                }
                @media #{$lg} {
                    right: -190px;
                }
            }
            &-3 {
                .owl-nav {
                    right: -310px;
                    @media #{$xxl} {
                        right: -285px;
                    }
                    @media #{$laptop} {
                        right: -235px;
                    }
                    @media #{$lg} {
                        right: -200px;
                    }
                    @media #{$md} {
                        right: -365px;
                    }
                }
            }
        }
    }
    &__col {
        & .nav-tabs {
            border: none;
            & .nav-item {
                &:not(:last-child) {
                    margin-right: 15px;
                }
                & .nav-link {
                    padding: 0;
                    font-size: 24px;
                    border: none;
                    color: #b6b6b6;
                    &.active {
                        color: $t-yellow;
                    }
                }
            }
        }
    }
    &__result {
        @media #{$xs} {
            padding-left: 0;
            margin-top: 10px;
        }
        & p {
            font-size: 14px;
            margin-bottom: 0;
        }
    }
    &__sorting {
        & .nice-select {
            border: 1px solid #e6e6e6;
            color: $black;
            height: 40px;
            line-height: 40px;
            @include border-radius(30px);
            padding-left: 30px;
            padding-right: 40px;
            &::after {
                right: 20px;
            }
        }
        &.product__show-position {
            & .nice-select {
                padding-left: 50px;
                padding-right: 70px;
                &::after {
                    right: 30px;
                }
            }
        }
    }
    &__color {
        & ul {
            @include flexbox();
            align-items: center;
            flex-wrap: wrap;
            & li {
                display: inline-block;
                margin-top: 5px;
                margin-bottom: 5px;
                @media #{$xs} {
                    margin-bottom: 10px;
                }
                &:not(:last-child) {
                    margin-right: 12px;
                }
                & a {
                    position: relative;
                    display: block;
                    width: 20px;
                    height: 20px;
                    @include border-radius(50%);
                    z-index: 1;
                    font-size: 0;
                    text-align: center;
                    line-height: 20px;
                    box-shadow: 0 0 0 1px #e6e6e6, inset 0 0 0 2px #fff;
                    background: #000;
                    & img {
                        width: 16px;
                        height: 16px;
                    }
                    &.selected {
                        box-shadow: 0 0 0 1px $black, inset 0 0 0 2px #fff;
                    }
                    &:hover {
                        box-shadow: 0 0 0 1px $black, inset 0 0 0 2px #fff;
                    }
                    &.blue {
                        background: blue;
                    }
                    &.red {
                        background: red;
                    }
                    &.yellow {
                        background: yellow;
                    }
                    &.pink {
                        background: pink;
                    }
                    &.brown {
                        background: brown;
                    }
                    &.green {
                        background: green;
                    }
                    &.oragne {
                        background: orange;
                    }
                }
            }
        }
    }
    &__widget {
        &-item {
            & .accordion-item:first-of-type .accordion-button {
                border-radius: 0;
            }
            & .accordion-button {
                padding: 0;
                outline: 0;
                box-shadow: none;
                padding-bottom: 6px;
                border-bottom: 1px solid $border;
                position: relative;
                font-size: 18px;
                font-weight: 500;
                color: $black;
                &::before {
                    position: absolute;
                    content: '';
                    bottom: -1px;
                    left: 0;
                    height: 2px;
                    width: 50px;
                    background: $t-yellow;
                }
                &::after {
                    background-image: none;
                    border-bottom: 1px solid #222;
                    border-right: 1px solid #222;
                    content: '';
                    display: block;
                    height: 8px;
                    width: 8px;
                    margin-top: -7px;
                    pointer-events: none;
                    position: absolute;
                    right: 12px;
                    top: 50%;
                    -webkit-transform-origin: 66% 66%;
                    -ms-transform-origin: 66% 66%;
                    transform-origin: 66% 66%;
                    -webkit-transform: rotate(-135deg);
                    -ms-transform: rotate(-135deg);
                    transform: rotate(-135deg);
                    -webkit-transition: all 0.15s ease-in-out;
                    transition: all 0.15s ease-in-out;
                }
                &.collapsed {
                    &::after {
                        -webkit-transform: rotate(45deg);
                        -ms-transform: rotate(45deg);
                        transform: rotate(45deg);
                    }
                }
            }
            & .accordion-button:not(.collapsed) {
                background: $white;
            }
            & .accordion-item {
                border: none;
            }
            & .accordion-body {
                padding: 0;
                padding-top: 20px;
                padding-bottom: 30px;
            }
        }
        &-title {
            padding: 0;
            outline: 0;
            box-shadow: none;
            padding-bottom: 6px;
            border-bottom: 1px solid $border;
            position: relative;
            font-size: 18px;
            font-weight: 500;
            color: $black;
            &::before {
                position: absolute;
                content: '';
                bottom: -1px;
                left: 0;
                height: 2px;
                width: 50px;
                background: $t-yellow;
            }
        }
    }
    &__size {
        & ul {
            & li {
                display: inline-block;
                margin-right: 5px;
                & a {
                    font-size: 12px;
                    color: $t-text-1;
                    display: inline-block;
                    width: 30px;
                    height: 30px;
                    text-align: center;
                    line-height: 28px;
                    border: 1px solid #dadada;
                    @include border-radius(50%);
                    &:hover {
                        color: $white;
                        background: $t-yellow;
                        border-color: $t-yellow;
                    }
                }
            }
        }
    }
    &__sm {
        &-item {
            &:not(:last-child) {
                margin-bottom: 20px;
                padding-bottom: 20px;
                border-bottom: 1px solid #f5f5f5;
            }
        }
        &-title {
            font-size: 14px;
            color: $black;
            font-weight: 400;
            & a {
                &:hover {
                    color: $t-yellow;
                }
            }
        }
        &-price {
            & .price {
                font-size: 16px;
                font-weight: 500;
                color: $black;
            }
        }
    }
    &__list {
        border-bottom: 1px solid #e6e6e6;
        &:hover {
            box-shadow: none;
        }
        & .product__thumb {
            & img {
                width: 332px;
                height: 332px;
                @media #{$xs} {
                    width: 100%;
                    height: auto;
                }
            }
        }
        & .product-name {
            font-size: 18px;
            font-weight: 400;
            color: $black;
            margin-bottom: 6px;
        }
        & .price {
            padding: 0;
            margin-bottom: 17px;
        }
        & .product-review-action {
            @media #{$lg, $xs} {
                margin-left: 0;
            }
            & span {
                padding: 0;
                padding-right: 10px;
                margin-right: 7px;
                font-size: 13px;
                line-height: 1;
                color: #989898;
                font-weight: 400;
                &:not(:last-child) {
                    padding-right: 10px;
                    margin-right: 7px;
                    border-right: 1px solid #e6e6e6;
                }
            }
        }
        & .product-text {
            padding: 0;
            margin-bottom: 20px;
        }
        & .product__action-list {
            position: relative;
            visibility: visible;
            opacity: 1;
            text-align: left;
            margin: inherit;
            bottom: inherit;
            & ul {
                @media #{$lg, $xs} {
                    margin-top: 10px;
                }
                & li {
                    margin: 0;
                    margin-right: 10px;
                }
            }
        }
        &-features {
            margin-bottom: 20px;
            & ul {
                & li {
                    list-style: disc;
                    margin-left: 13px;
                }
            }
        }
    }
    &__details {
        &-wrapper {
            @media #{$md, $sm, $xs} {
                margin-top: 30px;
            }
        }
        &-nav {
            &-thumb {
                & img {
                    @media #{$xxl, $laptop, $lg, $md, $sm, $xs} {
                        width: 100%;
                    }
                }
            }
            & .nav-tabs {
                border: none;
                & .nav-item {
                    padding: 0;
                    & .nav-link {
                        padding: 0;
                        margin: 0;
                        border: none;
                        padding: 5px 0;
                        border-top: 1px solid #ebebeb;
                        @include border-radius(0);
                        & img {
                            width: 85px;
                            height: 85px;
                        }
                    }
                }
            }
        }
        &-title {
            font-size: 24px;
        }
        & .rating__shop ul li a {
            font-size: 14px;
        }
        & .product__price {
            & span {
                font-size: 22px;
                &.new {
                    color: $t-yellow;
                }
                &.old {
                    color: #555;
                    font-size: 18px;
                    text-decoration: line-through;
                    margin-left: 10px;
                }
            }
        }
        &-stock {
            & h3 {
                font-size: 20px;
                font-weight: 400;
                margin-bottom: 17px;
                & span {
                    font-weight: 500;
                }
            }
            & .progress {
                height: 10px;
                border-radius: 10px;
                margin-bottom: 70px;
            }
            & .progress-bar {
                background-color: $t-yellow;
            }
        }
        &-action {
            & ul {
                & li {
                    display: inline-block;
                    margin-right: 4px;
                    & a {
                        display: block;
                        width: 45px;
                        height: 45px;
                        line-height: 45px;
                        background: $grey-4;
                        border: 1px solid $border;
                        @include border-radius(50%);
                        text-align: center;
                        &:hover {
                            color: $black;
                            background: $t-yellow;
                            border-color: $t-yellow;
                        }
                    }
                }
            }
        }
        &-des {
            & h3 {
                font-size: 18px;
                margin-bottom: 15px;
            }
            &-tab {
                & .nav-tabs {
                    border: 0;
                    margin: 0;
                    border-bottom: 1px solid #ebebeb;
                    & .nav-item {
                        margin: 0;
                        & .nav-link {
                            padding: 0;
                            padding-bottom: 5px;
                            border: 0;
                            position: relative;
                            font-size: 20px;
                            font-weight: 500;
                            color: $black;
                            margin-right: 30px;
                            color: #b6b6b6;
                            background: transparent;
                            &::after {
                                position: absolute;
                                content: '';
                                bottom: 0;
                                left: 0;
                                height: 2px;
                                width: 50px;
                                background: transparent;
                            }
                            &.active,
                            &:hover {
                                color: $black;
                                &::after {
                                    background-color: $t-yellow;
                                }
                            }
                        }
                    }
                }
            }
        }
        &-review {
            & .review {
                &-wrapper {
                    & .block-title {
                        font-size: 14px;
                        margin-bottom: 0;
                        border-bottom: 1px solid #ebebeb;
                    }
                }
                &-item {
                    border-bottom: 1px solid #ebebeb;
                    padding: 16px 0;
                    padding-bottom: 30px;
                }
                &-title {
                    font-size: 14px;
                    font-weight: 500;
                    color: $black;
                    margin-bottom: 7px;
                }
                &-ratings {
                    & ul {
                        line-height: 1;
                        & li {
                            line-height: 1;
                            display: inline-block;
                            margin-bottom: 5px;
                            & a {
                                font-size: 13px;
                                color: $t-yellow;
                            }
                        }
                    }
                    &-single {
                        & span {
                            color: #222;
                            width: 20%;
                            line-height: 1;
                            @media #{$xs} {
                                width: 30%;
                            }
                        }
                    }
                }
                &-text {
                    & p {
                        color: #222;
                    }
                }
                &-meta {
                    & span {
                        color: #222;
                        line-height: 1;
                    }
                }
                &-input {
                    width: 75%;
                    @media #{$xs} {
                        width: 65%;
                    }
                    & input,
                    & textarea {
                        width: 100%;
                        height: 30px;
                        line-height: 30px;
                        background: $white;
                        border: 1px solid #e6e6e6;
                        padding: 0 15px;
                        margin-bottom: 30px;
                    }
                    & textarea {
                        line-height: 1.1;
                        padding: 15px;
                        height: 105px;
                        outline: none;
                    }
                    &-title {
                        font-size: 12px;
                        color: #555;
                        width: 25%;
                        @media #{$xs} {
                            width: 35%;
                        }
                        &::after {
                            content: "*";
                            color: #e02b27;
                            font-size: 14px;
                            margin: 0 0 0 5px;
                        }
                    }
                    & .review-ratings-single {
                        & ul li a {
                            color: #c7c7c7;
                            &:hover {
                                color: $t-yellow;
                            }
                        }
                    }
                }
                &-form {
                    @media #{$md, $sm, $xs} {
                        margin-top: 35px;
                    }
                    & h3,
                    &>p {
                        font-size: 14px;
                        text-transform: uppercase;
                        font-weight: 500;
                        color: $black;
                    }
                }
            }
        }
        &-color {
            & span {
                font-size: 14px;
                color: $black;
                width: 8%;
                @media #{$sm} {
                    width: 13%;
                }
                @media #{$xs} {
                    width: 25%;
                }
            }
            & ul {
                line-height: 1;
                & li {
                    display: inline-block;
                    margin-right: 5px;
                    line-height: 1;
                    & a {
                        display: inline-block;
                        width: 32px;
                        height: 32px;
                        background: #000;
                        @include border-radius(50%);
                        &.blue {
                            background: blue;
                        }
                        &.red {
                            background: red;
                        }
                    }
                }
            }
        }
        &-size {
            & span {
                font-size: 14px;
                color: $black;
                width: 8%;
                @media #{$sm} {
                    width: 13%;
                }
                @media #{$xs} {
                    width: 25%;
                }
            }
            & ul {
                line-height: 1;
