        &-download {
            &-input {
                font-size: 14px;
                color: $black;
                & span {
                    display: block;
                    font-size: 13px;
                    &::after {
                        content: "*";
                        color: #e02b27;
                        font-size: 14px;
                        margin: 0 0 0 5px;
                    }
                }
                & label {
                    &:hover {
                        cursor: pointer;
                    }
                }
            }
            &-btn {
                margin-left: 290px;
                @media #{$laptop} {
                    margin-left: 180px;
                }
                @media #{$lg} {
                    margin-left: 80px;
                }
                @media #{$md} {
                    margin-left: 260px;
                }
                @media #{$sm} {
                    margin-left: 60px;
                }
                @media #{$xs} {
                    margin-left: 0px;
                    margin-top: 15px;
                }
                & a {
                    color: $t-yellow;
                }
            }
        }
        &-group {
            &-top {
                background: #f6f6f6;
                padding: 5px 5px;
                margin-bottom: 10px;
                & h4 {
                    font-size: 13px;
                    text-transform: uppercase;
