                & .price {
                    font-size: 14px;
                    font-weight: 500;
                    color: $black;
                    &.new-price {
                        color: $t-yellow;
                    }
                    &.old-price {
                        text-decoration: line-through;
                        color: #b1b1b1;
                        margin-left: 7px;
                    }
                }
            }
            & .product-group-quantity {
                & input {
                    border: 1px solid #ebebeb;
                    width: 40px;
                    height: 40px;
                    line-height: 40px;
                    text-align: center;
                }
            }
        }
    }
    &__filter {
        &-right {
            @media #{$sm, $xs} {
                margin-top: 30px;
            }
        }
    }
}

.cart {
    &__toggle {
        position: relative;
        width: 50px;
        height: 50px;
        display: inline-block;
        border: 2px solid $border;
        @include border-radius(100%);
        margin-right: 25px;
        &::after {
            content: "\f290";
            font-family: 'Font Awesome 5 Pro';
            font-size: 18px;
            color: #222;
            position: absolute;
            top: 0;
            margin-right: 2px;
            line-height: 46px;
            right: 12px;
        }
        &:hover {
            background: $t-yellow;
            color: $white;
            border-color: $t-yellow;
            &::after {
                color: $white;
            }
            & .cart__total-item {
                background: $black;
                color: $white;
            }
        }
        &.cart__toggle-open {
            background: $t-yellow;
            color: $white;
            border-color: $t-yellow;
            &::after {
                color: $white;
            }
            & .cart__total-item {
                background: $black;
                color: $white;
            }
        }
    }
    &__mini {
        position: absolute;
        top: 120%;
        right: 0;
        width: 350px;
        background: $white;
        @include box-shadow(0 6px 12px rgba(0, 0, 0, .175));
        z-index: 11;
        border-top: 2px solid $t-yellow;
        padding: 35px 15px;
        padding-top: 27px;
        visibility: hidden;
        opacity: 0;
        @include transition(.2s);
        &.cart__opened {
            visibility: visible;
            opacity: 1;
            top: 100%;
        }
        &-4 {
            border-color: $t-red-dark;
            & .cart__price {
                & span {
                    color: $t-red-dark;
                }
            }
            & .cart__sub-total {
                color: $t-red-dark;
            }
        }
    }
    &__close {
        position: absolute;
        top: 15px;
        right: 15px;
        &-btn {
            font-size: 16px;
            color: $black;
            background: transparent;
            &:hover {
                color: $t-yellow;
            }
        }
    }
    &__title {
        border-bottom: 1px solid $border;
        padding-bottom: 20px;
        & h4 {
            font-size: 18px;
            display: inline-block;
            margin-right: 5px;
            margin-bottom: 0;
        }
        & span {
            font-size: 12px;
        }
    }
    &__mini-wrapper {}
    &__total-item {
        min-width: 20px;
        height: 20px;
        text-align: center;
        line-height: 20px;
        position: absolute;
        top: -2px;
        left: 35px;
        color: $white;
        background: $t-yellow;
        border-radius: 100%;
        font-size: 10px;
    }
    &__content {
        & span {
            display: block;
        }
    }
    &__item {
        padding-bottom: 20px;
        padding-top: 20px;
        border-bottom: 1px solid $border;
    }
    &__price {
        & span {
            color: $t-yellow;
        }
    }
    &__sub {
        padding-top: 20px;
        padding-bottom: 20px;
        & h6 {
            margin-bottom: 0;
        }
    }
    &__sub-total {
        font-size: 14px;
        color: $black;
        font-weight: 500;
        color: $t-yellow;
    }
    &__thumb {
        margin-right: 15px;
        & img {
            width: 70px;
            height: 70px;
        }
    }
    &__details {}
    &__del {}
}


